<template>
    <div class="dv-landing">
        <div 
        class="dv-menu-btn"
        @click="menubtn = !menubtn">
            <div class="line one" :class="{ 'on': menubtn }"></div>
            <div class="line two" :class="{ 'on': menubtn }"></div>
            <div class="line three" :class="{ 'on': menubtn }"></div>
        </div>      
        <div 
        class="dv-background" 
        :class="{ 'shift-left': menubtn }"
        @click="clickBackLanding">
            <landing-intro></landing-intro>
        </div>
        <div 
        class="dv-menu" 
        :class="{ 'shift-left-menu': menubtn }">
            <landing-menu @clickMenuItem="showPage" :menubtn="menubtn"></landing-menu>
        </div>
        <div 
        class="dv-page-project dv-page"
        :class="{ 'shift-right-page': pageOn == 1 }">
        </div>
        <div 
        class="dv-page-about dv-page"
        :class="{ 'shift-right-page': pageOn == 2 }">
        </div>
        <div 
        class="dv-page-contact dv-page"
        :class="{ 'shift-right-page': pageOn == 3 }">
        </div>
    </div>
</template>

<script>
import LandingIntro from './LandingIntro.vue'
import LandingMenu from './LandingMenu.vue'
export default {
    data() {
        return {
            menubtn: false,
            pageOn: 0,
        }
    },
    components: {
        LandingIntro,
        LandingMenu
    },
    methods: {
        clickBackLanding(){
            this.menubtn = false
        },
        showPage(index){
            this.pageOn = index
            if(this.pageOn == 0){
                this.menubtn = false
            }
        }
    },
    watch: {
        menubtn: function(){
            if(!this.menubtn){
                this.pageOn = 0
            }
        }
    }
}
</script>

