<template>
    <div class="dv-landing-menu">
        <div 
        class="menu-item"
        v-for="(item, index) in menuItem"
        :key="item + index">
            <span 
            @click="activeMenu(index)"
            :class="{ 'active': menuActive[index] }">{{ item }}</span>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            menuItem: ['Home', 'Project', 'About', 'Contact'],
            menuActive: [true, false, false, false],
            lastActive: 0
        }
    },
    methods: {
        activeMenu(index){
            if(this.lastActive != undefined){
                this.menuActive[this.lastActive] = false
            }
            this.menuActive[index] = true
            this.lastActive = index
            this.menuActive = JSON.parse(JSON.stringify(this.menuActive))
            this.$emit('clickMenuItem', index)
        }
    },
    watch: {
        menubtn: function(){
            if(!this.menubtn){
                this.menuActive = [true, false, false, false]
                this.lastActive = 0
            }
        }
    },
    props: {
        menubtn: Boolean
    }
}
</script>

<style lang="stylus" scoped>
*
    position relative
.dv-landing-menu
    width 100%
    height 80%
.menu-item
    top 30%
    margin-top 30px
    text-align left
    padding 0px 100px
    span 
        font-weight 900
        font-size 20px
        cursor pointer
        font-family Verdana, Geneva, Tahoma, sans-serif
        transition 0.5s
        color black
        &:hover
            color #64b2cd
    .active
        color #64b2cd
</style>